.header {
  background-color: var(--background-primary-color);
  width: 100vw;
  box-shadow: 0px 8px 24px rgba(149, 157, 165, 0.2);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  &Content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    @include media(medium) {
      justify-content: space-around;
      flex-wrap: nowrap;
      height: 11.3rem;
    }
    @include media(extra-large) {
      justify-content: space-between;
    }
  }

  /* burger */
  img:nth-child(2) {
    @include media(medium) {
      display: none;
    }
  }

  &-social {
    display: flex;
    gap: 3.5rem;
    justify-content: center;
    width: 100%;
    margin-top: 3.5rem;
    @include media(medium) {
      width: initial;
      margin: 0;
    }
    img {
      filter: invert(43%) sepia(7%) saturate(1152%) hue-rotate(172deg) brightness(91%) contrast(85%);
    &:hover {
      filter: invert(46%) sepia(83%) saturate(1927%) hue-rotate(317deg) brightness(102%) contrast(101%);
      transition: 0.2s;
    }
}
  }

  &-mobile {
    display: none;
  }
}

.hiddenMobile {
  img {
    filter: invert(43%) sepia(7%) saturate(1152%) hue-rotate(172deg)
      brightness(91%) contrast(85%);
    &:hover {
      filter: invert(54%) sepia(76%) saturate(3306%) hue-rotate(316deg)
        brightness(100%) contrast(102%);
      transition: 0.2s;
    }
  }
  display: none;

  @include media(medium) {
    display: flex;
  }

  li{
    transition: 0.9s;
    &:hover{
        transform: scale(1.1);
    }
  }
}

.nav-open {
  display: flex;
  flex-direction: column;
  gap: 3.5rem;
  width: 100%;
  padding-top: 1.5rem;
  background-color: red;
}
